.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#container {
  border: solid rgb(133, 109, 109);
  border-width: 100px;
  padding: 50px;
  
}
.header {
  text-align: center;
  padding-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif, Helvetica, sans-serif;
}
#searchButton {
  padding: 8px 9px;
  font-size: 15px;
  background-color: rgb(247, 182, 182);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 20px;
}
#searchButton:hover {
  background-color: rgb(133, 109, 109);
}
#input {
  width: 300px;

  
